.map-content {
  width: 100%;
  height: 100%;
  overflow: hidden; }

body, html, #app {
  color: #323232;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-size: cover;
  background-color: #030408;
  overflow: hidden; }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0; }

ul, li {
  list-style: none; }

.clearfix {
  *zoom: 1; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

:focus {
  outline: none; }

a {
  text-decoration: none;
  outline: none;
  color: #fff; }

.pull-left {
  float: left !important; }

.pull-right {
  float: right !important; }

.main {
  margin: 50px auto;
  padding: 0 15px;
  text-align: left; }

.charts-list {
  margin: 0 auto; }

.chart {
  padding: 10px 10px 0;
  height: 278px;
  width: 282px;
  border: 1px solid #e5e5e5;
  display: inline-block;
  margin: 36px 5px;
  cursor: inherit;
  border-radius: 2px;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  background-color: #fff; }

.chart .chart_wrap {
    height: 262px;
    width: 262px;
    margin: auto; }

.chart .chart_bg {
    width: 262px;
    height: 206px;
    background: center no-repeat;
    background-size: cover;
    display: block; }

.chart .chart_hover {
    color: #fff;
    text-align: center;
    height: 100%; }

.chart .chart_magnifier {
    padding-top: 70px; }

.chart .chart_info {
    height: 56px;
    padding: 0 3px; }

.chart .chart_name, .chart .chart_all {
    font-size: 14px;
    color: #767676;
    line-height: 30px;
    text-align: left; }

.chart .chart_all {
    text-align: center;
    padding-top: 28px; }

.chart .chart_detail {
    font-size: 12px;
    color: #999; }

.chart .chart_icon {
    display: inline-block;
    height: 16px;
    width: 16px;
    background: url(data:image/png;base64,) no-repeat;
    background-position: 0 0;
    vertical-align: middle;
    margin-right: 2px; }

.chart .chart_author_icon {
    background-position: 0 0; }

.chart .chart_time_icon {
    background-position: -16px 0; }

.chart .chart_icontxt {
    line-height: 16px;
    vertical-align: middle; }

.chart .chart_magnifier_right {
    opacity: 0;
    width: 100%;
    height: 100%;
    float: right;
    text-align: center;
    line-height: 206px; }

.chart .chart_magnifier_right div:nth-child(1) {
      height: 60%; }

.chart .chart_magnifier_right div:nth-child(2) {
      height: 28px;
      line-height: 28px; }

.chart .chart_magnifier_right:hover {
      opacity: 1;
      cursor: pointer; }

.chart .chart_magnifier_right:hover .chart_hover {
        background: rgba(0, 0, 0, 0.3); }

.chart:hover {
    -webkit-box-shadow: 0 0 16px rgba(0, 0, 0, 0.45);
            box-shadow: 0 0 16px rgba(0, 0, 0, 0.45); }

@media (min-width: 992px) {
  .charts-list {
    width: 921px; } }

@media (min-width: 1280px) {
  .charts-list {
    width: 1230px; } }

@media (min-width: 1600px) {
  .charts-list {
    width: 1537px; } }

@media only screen and (max-width: 767.99px) {
  #charts-list-ul {
    text-align: center; } }

