.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 50px;
  border: none;
  padding-right: 15px;
  padding-left: 15px;
  background-color: #293c55;
  -webkit-transition: background-color 0.5s linear;
  transition: background-color 0.5s linear;
  background-color: rgba(41, 60, 85, 0.4); }

.header .navbar-header a {
    float: left;
    color: #777;
    height: 50px;
    padding: 15px 15px;
    font-size: 18px;
    line-height: 20px; }

.header .navbar-header .navbar-logo {
    display: block;
    height: 32px;
    margin-top: -6px;
    margin-left: -2px; }

.navbar-nav {
  -webkit-transition: background-color 0.5s linear;
  transition: background-color 0.5s linear; }

.navbar-nav li {
    position: relative;
    float: left; }

.navbar-nav li a {
      color: #fff;
      background-color: transparent;
      font-size: 14px;
      position: relative;
      line-height: 20px;
      display: block;
      padding: 15px 20px;
      -webkit-transition: 0.5s background-color;
      transition: 0.5s background-color; }

.navbar-nav li a:before {
        content: '';
        position: absolute;
        left: 50%;
        right: 50%;
        top: 0;
        background: #42B983;
        height: 4px;
        -webkit-transition-duration: .3s;
                transition-duration: .3s;
        -webkit-transition-timing-function: ease-out;
                transition-timing-function: ease-out; }

.navbar-nav li a:hover {
        background-color: #162436; }

.navbar-nav li a:hover:before {
          left: 0;
          right: 0; }

.navbar-nav li a:focus:before {
        left: 0;
        right: 0; }

.navbar-nav .active > a {
    padding-top: 11px;
    color: #fff;
    background-color: transparent;
    border-top: 4px solid #42B983;
    -webkit-transition: 0.5s background-color;
    transition: 0.5s background-color; }

.navbar-nav .active > a:hover {
      color: #f9f9f9;
      background-color: #162436; }

.navbar-left {
  float: left; }

.navbar-right {
  float: right;
  margin-right: -15px;
  -webkit-transition: background-color 0.5s linear;
  transition: background-color 0.5s linear; }

.render-content {
  width: 100%;
  height: 100%; }

